@import url('https://fonts.googleapis.com/css2?family=Karla&family=Rubik&display=swap');

:root {
  --main-bg: #ffffff;
  --main-text:  #1b2733;
  --alt-text:  #e0e0e0;
  --gradient1: rgba(7, 37, 75, 0.3);
  --gradient2: rgba(157, 157, 182, 0.4);
  --gradient3: rgba(56, 100, 141, 0.3);
  --gradient4: rgba(56, 100, 141, 0);
  --main-dark: rgba(38, 38, 114, 0.8);
  --table-bg: white;

  --alt-bg: #637282;
  --h1-color: var(--alt-text);
  --header-bg: var(--main-dark);
  --alt-bg-opaque: rgba(38, 38, 114, 0.5);

  --header-text: #ffffff;
  --button: var(--main-dark);
  --eligible: #00ff99;
  --error: rgba(238, 11, 11, 0.651);
}

/* ðŸŽ¨ colors: #ffffff | #1b2733 | #637282 | #AB24A1 */

main {
  display: flex;
  height: 100vh;
  overflow-y: scroll;
  max-height: 95%;
  background: var(--main-bg);
  background: whitesmoke;      
  grid-template-columns: minmax(100%, 10%);
  text-align: center;
  font-family: 'Karla', 'Rubik', sans-serif;
  /* position: relative; */
  margin: 0;
  width: calc(100vw - 270px);
  /* padding-right: 22vw; */
  min-width: 600px;
  /* margin: 0 auto; */
  padding: 0;

  /* font-family: 'Rubik', sans-serif; */
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1 {
  text-align: center;
  margin: 50px auto;
  padding: 5px;
  background: var(--alt-bg-opaque);
  max-width: 100%;
  color: var(--h1-color);
  font-size: 2rem;
}

h2 {
  background-color: transparent;
  border-radius: 8px 8px 0 0;
  font-size: 1.7rem;
  color: var(--header-text);
  margin: 0 auto;
  text-align: center;
  padding: 5px 5px;
}

h3 {
  color: var(--alt-bg);
  font-size: 1.6rem;
  margin: 30px 0 0;
  padding: 5px;
  width: auto;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.App {
  text-align: center;
}

button {
  border-radius: 4px;
  box-shadow: -1px 2px grey;
  border: 1px solid var(--button);
  color: whitesmoke;
  background: var(--gradient1);
}

.save-button {
  max-width: 100px;
  margin: 15px auto;
}

.button-large {
  font-size: 1.4rem;
  padding: 7px 15px;
}

button:hover {
  background: var(--gradient3);
}

.hidden {
  display: none;
}

.margin-15 {
  margin: 15px;
}

.margin-50 {
  margin: 50px;
}

textarea {
  width: 90%;
}

@media (max-width: 800px) {
  body {
    overflow-x: scroll;
  }
  main {
    margin-top: 55px;
    width: 100vw;
  }

  main > div {
    width: 100%;
  }
}
