@import url(https://fonts.googleapis.com/css2?family=Karla&family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.errorRow {
  margin: 15px;
  text-align: center;
  color: var(--error);
  font-weight: 800;
}

.errorText {
  color: var(--error);
}

.login-container {
  width: 100vw;
  padding: 0;
}

.connect-to-yahoo a {
  border: 2px solid black;
  border-radius: 4px;
  padding: 10px;
  background-color: oldlace;
  color: black;
  font-weight: bold;
  text-decoration: none;
}

.connect-to-yahoo a:hover {
  background-color: var(--gradient1);
}

ul {
  display: flex;
  justify-content: space-around;
}

li.react-tabs__tab {
  padding: 5px 10px 5px;
  position: relative;
  border: 1px solid var(--gradient1);
  box-shadow: -2px -1px grey;
  border-radius: 8px 8px 0 0;
  border-bottom: none;
  bottom: 10px;
  cursor: pointer;
  background: whitesmoke;
}

.react-tabs__tab-panel {
  transition: background 0.2s ease;
  padding: 0 20px;
  margin-top: 0;
}

#react-tabs-1 > div {
  padding-top: 20px;
}

#react-tabs-3 > div {
  padding-top: 18px;
}

#react-tabs-5 > div {
  padding-top: 16px;
}

#react-tabs-7 > div {
  padding-top: 14px;
}

.react-tabs__tab:hover {
  background: var(--gradient1);
  transition: background 0.2s ease;
}

li.react-tabs__tab--selected:hover {
  cursor: auto;
}

li.react-tabs__tab--selected, .react-tabs__tab-panel {
  border: 1px solid transparent;
  background: var(--gradient1);
  color: whitesmoke;
  transition: background 0.2s ease;
}

.navbar-tabs {
  padding: 20px 0 10px 0;
  font-size: 1.2rem;
  width: calc(100vw - 250px);
  min-width: 550px;
}

.react-tabs__tab--selected {
  font-weight: bold;
  position: fixed;
}

.navbar-tabs > div {
  position: relative;
  width: 100%;
  top: -10px;
}

.teamLogoContainer {
  margin: 0;
  display: flex;
  place-content: center;
}

.teamLogo {
  height: 35px;
  width: auto;
}

@media screen and (max-width: 800px) { 
  .navbar-tabs > div {
    width: 100vw;
  }
  .navbar-tabs > ul {
    width: 100vw;
  }
  ul.react-tabs__tab-list:last-child, li.react-tabs__tab > div {
    display: none;
  }
  .react-tabs__tab-panel {
    padding: 0;
  }
  th {
    width: auto;
  }
}

.search-filter {
  width: 100%;
  margin: 0;
}

.team-filter {
  width: 50px;
}

table {
  background: var(--table-bg);
  box-shadow: -3px -3px gray;
  border-spacing: 0;
  text-align: left;
  border-radius: 4px;
  padding: 0; 
  font-size: 0.9rem;
  margin: 0;
  width: 100vw;
  max-width: 100%;
  padding-right: 22vw;
  -webkit-column-width: auto;
          column-width: auto;
  /* max-width: calc(100vw - 250px); */
}

.table thead th {
  vertical-align: top;
  font-size: 1.1rem;
  padding: 4px;
}

thead, tbody {
  max-width: 100%;
}

/* .tableWrap {
  display: block;
  max-width: 100%;
  overflow-x: scroll;
  overflow-y: hidden;
  border-bottom: 1px solid black;
} */

tr:nth-child(odd) {
  background-color: rgba(164, 189, 205, 0.7);
}

tr:nth-child(even) {
  background-color: rgba(200, 233, 255, 0.7);
}

tr:hover {
  background-color: rgb(166, 220, 250);
}

th, td {
  margin: 0;
  padding: 2px;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
}

td:last-child, th:last-child {
  border-right: 0;
}

table tr:last-child td {
  border-bottom: 0;
}

.blank-cell {
  background-color: transparent;
}

.change-user-dropdown {
  width: 100px;
}

.draft-button-cell {
  width: 20px;
}

.draft-button-cell div {
  display: flex;
  justify-content: center;
}

.disabled-1 {
  background-color: #de7d7d !important;
}

.playerNameAndHeadshot {
  display: flex;
  align-items: center;
}

.headshot {
  height: 35px;
  width: auto;
  margin: 0 10px 0 0;
}

.prospect {
  font-size: 0.8rem;
  background: lightcyan;
  border: 1px solid black;
  border-radius: 100%;
  padding: 4px 7px;
}

.keeper {
  font-size: 0.8rem;
  background: black;
  color: white;
  border-radius: 2px;
  padding: 3px 6px;
}

#prospect-column, #team-dropdown-column {
  background: transparent;
  position: absolute;
  top: 0;
  left: 270px;
  border: none;
}

.prospect-column-hidden {
  width: 0;
  padding: 0 !important;
  border: none;
}

.post-title {
  padding: 0 !important;
  margin: 0 !important;
}

.post-title > div {
  vertical-align: center;
  cursor: pointer;
  width: 100%;
  height: 100%;
  padding: 20px;
}

@media screen and (max-width: 800px) { 
  table {
    width: 800px;
    margin: 0;
    padding: 0;
  }
  th.blank-cell, .admin-column, select.change-user-dropdown {
    display: none;
  }
  .table thead th, .table td {
    font-size: 0.7rem;
    padding: 3px;
  }
  .headshot {
    display: none;
  }
  .draftPickTimestamp {
    font-size: 0.7rem;
  }
  .timezoneInWords {
    display: none;
  }
  .teamLogo {
    height: 25px;
    width: auto;
  }
  .teamLogoContainer {
    height: 100%;
    min-height: 50px;
    align-items: center;
  }
  #prospect-column, #team-dropdown-column {
    top: 12px;
    left: 175px;
  }
}


.pagination {
  display: flex;
  justify-content: space-between;
  width: 100%;
  min-width: 550px;
  padding: 0 40px 0 0;
  margin: 0 0 20px 0;
}

.round {
  font-weight: bold;
  font-size: 1.7rem !important;
  color: white;
}

.pagination-arrows {
  display: flex;
  color: black !important;
  cursor: pointer;
}

.pagination-arrows span {
  color: #000 !important;
}

.pagination li, .pagination select {
  height: 33px;
  width: 250px;
}

.pagination li.pagination-goto-page {
  display: flex;
  flex-direction: row;
  width: 200px;
  border-radius: 4px;
}

.page-link {
  display: flex;
  color: black;
}

.page-of-page {
  width: 70px;
}

.pagination-goto-page a {
  width: 200px;
}

.pagination-goto-page .page-link:first-child {
  border-radius: 4px 0 0 4px;
}

.pagination-goto-page .page-link:last-child {
  border-radius: 0 4px 4px  0;
}

.pagination li {
  font-size: 0.9rem;
  width: 130px;
}

@media (max-width: 800px) {
  .pagination li.pagination-goto-page {
    display: none;
  }
}

.ReactModal__Content ReactModal__Content--after-open {
  min-width: 500px;
  height: 80vh;
  min-height: 400px; 
  /* margin-left: 25vw; */
}

#draft-player-modal {
  width: 50vw;
  height: 380px;
  margin: auto;
}

#forum-post-modal, #new-forum-post-modal, #rule-modal, #new-rule-modal {
  background-color: wheat;
  width: calc(90vw - 250px);
  margin: 0;
  height: 85vh;
}

.ReactModal__Overlay--after-open {
  background-color: #5e8b950a !important
}

.ReactModal__Content {
  width: 70vw;
  min-width: 400px;
  margin-left: 15vw;
  height: 500px;
  margin-top: 50px;
}

.close-button-wrapper {
  display: flex;
  justify-content: right;
}

.close-modal {
  display: flex;
  position: fixed;
}

.modal-title {
  font-size: 1.6rem;
  padding: 15px 35px 35px 35px;
  text-align: center;
  font-weight: 800;
}

.modal-forum-user {
  display: flex;
  margin: 10px 10px 0 40px;
  font-weight: 700;
}

.modal-player-info {
  margin: 20px auto;
  font-size: 1.4rem;
  font-weight: bold;
  text-align: center;
}

.button-group {
  max-width: 500px;
  display: flex;
  justify-content: space-around;
  margin: 80px auto;
} 

.modal-forum-text {
  border: 1px solid gainsboro;
  border-radius: 4px;
  background: whitesmoke;
  text-align: left;
  font-size: 1.1rem;
  margin: 10px 25px 25px 25px;
  padding: 5px;
}

.modal-forum-text > p {
  margin: 5px;
  padding: 5px;
}

.modal-forum-date {
  font-style: italic;
  font-size: 0.7rem;
  font-weight: 400;
  padding: 4px 0 0 0;
}

.forum-reply {
  padding-left: 50px;
}

.new-post-form {
  display: flex;
  flex-direction: column;
}

.new-post-form > div {
  display: flex;
  padding: 10px 20px;
  justify-content: space-around;
  flex-direction: column;
}

.new-post-form label {
  display: flex;
  font-weight: bold;
}

.new-post-form input, .new-post-form textarea {
  width: 90%;
}

@media screen and (max-width: 800px) { 
  .ReactModal__Content {
    width: calc(95vw - 150px) !important;
    min-width: 300px !important;
    margin-left: 0 !important;
    height: 95vh;
    margin-top: 10px;
  }
}


.loading-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: transparent;
  transition: background 0.15s ease;
  height: 100vh;
  width: calc(100vw - 250px);
  color: whitesmoke;
  padding-bottom: 20vh;
  z-index: 99;
}

.loading-gif {
  display: flex;
  justify-content: center;
  align-items: center;
}

.user-wrapper {
  display: flex;
  height: 30px;
}

.userInDraft {
  display: flex;
  align-items: center;
  font-size: 0.7rem;
  border-radius: 0 8px 8px 0;
  padding: 0 4px;
  text-align: center;
  color: darkorchid;
  font-weight: bold;
  background: var(--gradient3);
}

.admin-form {
  margin: 10px auto 100px;
}

.admin-form > h2 {
  margin: 50px;
}

.admin-form > div {
  display: flex;
  margin-left: 15vw;
}

.admin-form > div > label {
  text-align: left;
}

.admin-form > div > input, .admin-form > div > label, .wide-filter {
  width: 200px;
}

.positions {
  margin: 0;
  flex-direction: row !important;
  text-align: left;
}

.positions > input {
  margin: 0 0 0 15px;
}

.positions > label {
  margin: 10px;
}

.picks-iframe {
  width: 1200px;
  height: 650px;
}


#chatbox {
  position: fixed;
  display: flex;
  justify-content: left;
  top: 95px;
  right: 0;
  margin: 0 0 0 10px;
  padding: 10px;
  border-left: 2px solid black;
  border-top: 2px solid black;  
  height: calc(100vh - 45px);
  width: 270px;
  background-color: whitesmoke;
}

#chat-messages {
  position: fixed;
  display: flex;
  justify-content: left;
  text-align: left;
  top: 130px;
  right: 0;
  width: 270px;
  height: calc(100vh - 190px);
  padding: 5px;
  margin: 2px;
  word-wrap: break-word;
  overflow: scroll;
}

#chat-messages > li > div {
  margin-left: 10px;
}

#chat-title {
  position: relative;
  bottom: 40px;
  font-size: 1.1rem;
}

.user-wrapper {
  display: flex;
}

.user {
  color: darkorchid;
  border-radius: 0 8px 8px 0;
  font-weight: bold;
  font-size: 0.7rem;
  margin: 4px;
  padding: 4px;
  background: var(--gradient3);
}

.user-icon {
  font-size: 1.1rem;
  border-radius: 8px 0 0 8px;
  padding: 2px !important;
  height: 30px;
}

.message {
  word-break: break-all;
  word-break: break-word;
  font-size: 0.8rem;
}

.chat-announcement {
  font-weight: bold;
  font-style: italic;
}

#messageInput {
  position: fixed;
  bottom: 10px;
  right: 10px;
  width: 250px;
  padding: 5px;
}

#send-message-button {
  position: fixed;
  right: -200px;
  /* display: none; */
}


@media (max-width: 800px) {
  #chatbox {
    display: none;
  }
}

#countdown {
	color: red;
	border: none;
	border-radius: 8px;
	background-color: whitesmoke;
	position: relative;
	right: 2px;
	bottom: 10px;
	padding: 0 5px;
	font-weight: bold;
	font-size: 1.4rem;
}

.drafting-now {
	background: whitesmoke;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	border-right: 2px solid grey;
  border-left: 2px solid black;
	width: 65%;
	min-width: 140px;
  border-bottom: none;
	padding: 5px 0 0 5px;
	text-align: left;
}

.drafting-1, .drafting-true {
	background-color: #7ef20b7a;
}

.drafting-now :first-child {
  font-weight: bold;
  padding: 0 5px 0 0;
  margin: 0;
}

@media (max-width: 800px) {
  .drafting-now {
		flex-direction: row;
    width: 100vw;
    display: flex;
		border: none;
	}

	.drafting-now :first-child {
		margin-bottom: 12px;
	}
	
	#countdown {
		position: static;
		min-width: 90px;
		max-width: 110px;
	}
}

.widget-wrapper {
  background-color: transparent;
  position: fixed;
  display: flex;
  flex-direction: row;
  top: 0;
  right: 0;
  width: 270px;
}

#logout {
  /* position: fixed; */
  top: 5px;
  right: 12px;
  z-index: 99;
}

.logo-and-logout-wrapper {
  background-color: whitesmoke;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  height: 90px;
  width: 35%;
  align-items: center;
  margin-right: 5px;
}

.logo-wrapper {
  display: flex;
  align-items: center;
  top: 35px;
  right: 28px;
}

.logo {
  width: 30px;
  height: auto;
}

@media (max-width: 800px) {
  .widget-wrapper {
    height: 60px;
    width: auto;
    background-color: white;
	}
  
  .logo-and-logout-wrapper {
    display: relative;
    width: 0;
  }

  .logo-wrapper {
    display: none;
  }

  #logout {
    position: fixed !important;
    top: 2px;
    right: 2px;
    display: block;
    font-size: 0.7rem;
  }
}

:root {
  --main-bg: #ffffff;
  --main-text:  #1b2733;
  --alt-text:  #e0e0e0;
  --gradient1: rgba(7, 37, 75, 0.3);
  --gradient2: rgba(157, 157, 182, 0.4);
  --gradient3: rgba(56, 100, 141, 0.3);
  --gradient4: rgba(56, 100, 141, 0);
  --main-dark: rgba(38, 38, 114, 0.8);
  --table-bg: white;

  --alt-bg: #637282;
  --h1-color: var(--alt-text);
  --header-bg: var(--main-dark);
  --alt-bg-opaque: rgba(38, 38, 114, 0.5);

  --header-text: #ffffff;
  --button: var(--main-dark);
  --eligible: #00ff99;
  --error: rgba(238, 11, 11, 0.651);
}

/* ðŸŽ¨ colors: #ffffff | #1b2733 | #637282 | #AB24A1 */

main {
  display: flex;
  height: 100vh;
  overflow-y: scroll;
  max-height: 95%;
  background: #ffffff;
  background: var(--main-bg);
  background: whitesmoke;      
  grid-template-columns: minmax(100%, 10%);
  text-align: center;
  font-family: 'Karla', 'Rubik', sans-serif;
  /* position: relative; */
  margin: 0;
  width: calc(100vw - 270px);
  /* padding-right: 22vw; */
  min-width: 600px;
  /* margin: 0 auto; */
  padding: 0;

  /* font-family: 'Rubik', sans-serif; */
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1 {
  text-align: center;
  margin: 50px auto;
  padding: 5px;
  background: rgba(38, 38, 114, 0.5);
  background: var(--alt-bg-opaque);
  max-width: 100%;
  color: #e0e0e0;
  color: var(--h1-color);
  font-size: 2rem;
}

h2 {
  background-color: transparent;
  border-radius: 8px 8px 0 0;
  font-size: 1.7rem;
  color: #ffffff;
  color: var(--header-text);
  margin: 0 auto;
  text-align: center;
  padding: 5px 5px;
}

h3 {
  color: #637282;
  color: var(--alt-bg);
  font-size: 1.6rem;
  margin: 30px 0 0;
  padding: 5px;
  width: auto;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.App {
  text-align: center;
}

button {
  border-radius: 4px;
  box-shadow: -1px 2px grey;
  border: 1px solid rgba(38, 38, 114, 0.8);
  border: 1px solid var(--button);
  color: whitesmoke;
  background: rgba(7, 37, 75, 0.3);
  background: var(--gradient1);
}

.save-button {
  max-width: 100px;
  margin: 15px auto;
}

.button-large {
  font-size: 1.4rem;
  padding: 7px 15px;
}

button:hover {
  background: rgba(56, 100, 141, 0.3);
  background: var(--gradient3);
}

.hidden {
  display: none;
}

.margin-15 {
  margin: 15px;
}

.margin-50 {
  margin: 50px;
}

textarea {
  width: 90%;
}

@media (max-width: 800px) {
  body {
    overflow-x: scroll;
  }
  main {
    margin-top: 55px;
    width: 100vw;
  }

  main > div {
    width: 100%;
  }
}

