table {
  background: var(--table-bg);
  box-shadow: -3px -3px gray;
  border-spacing: 0;
  text-align: left;
  border-radius: 4px;
  padding: 0; 
  font-size: 0.9rem;
  margin: 0;
  width: 100vw;
  max-width: 100%;
  padding-right: 22vw;
  column-width: auto;
  /* max-width: calc(100vw - 250px); */
}

.table thead th {
  vertical-align: top;
  font-size: 1.1rem;
  padding: 4px;
}

thead, tbody {
  max-width: 100%;
}

/* .tableWrap {
  display: block;
  max-width: 100%;
  overflow-x: scroll;
  overflow-y: hidden;
  border-bottom: 1px solid black;
} */

tr:nth-child(odd) {
  background-color: rgba(164, 189, 205, 0.7);
}

tr:nth-child(even) {
  background-color: rgba(200, 233, 255, 0.7);
}

tr:hover {
  background-color: rgb(166, 220, 250);
}

th, td {
  margin: 0;
  padding: 2px;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
}

td:last-child, th:last-child {
  border-right: 0;
}

table tr:last-child td {
  border-bottom: 0;
}

.blank-cell {
  background-color: transparent;
}

.change-user-dropdown {
  width: 100px;
}

.draft-button-cell {
  width: 20px;
}

.draft-button-cell div {
  display: flex;
  justify-content: center;
}

.disabled-1 {
  background-color: #de7d7d !important;
}

.playerNameAndHeadshot {
  display: flex;
  align-items: center;
}

.headshot {
  height: 35px;
  width: auto;
  margin: 0 10px 0 0;
}

.prospect {
  font-size: 0.8rem;
  background: lightcyan;
  border: 1px solid black;
  border-radius: 100%;
  padding: 4px 7px;
}

.keeper {
  font-size: 0.8rem;
  background: black;
  color: white;
  border-radius: 2px;
  padding: 3px 6px;
}

#prospect-column, #team-dropdown-column {
  background: transparent;
  position: absolute;
  top: 0;
  left: 270px;
  border: none;
}

.prospect-column-hidden {
  width: 0;
  padding: 0 !important;
  border: none;
}

.post-title {
  padding: 0 !important;
  margin: 0 !important;
}

.post-title > div {
  vertical-align: center;
  cursor: pointer;
  width: 100%;
  height: 100%;
  padding: 20px;
}

@media screen and (max-width: 800px) { 
  table {
    width: 800px;
    margin: 0;
    padding: 0;
  }
  th.blank-cell, .admin-column, select.change-user-dropdown {
    display: none;
  }
  .table thead th, .table td {
    font-size: 0.7rem;
    padding: 3px;
  }
  .headshot {
    display: none;
  }
  .draftPickTimestamp {
    font-size: 0.7rem;
  }
  .timezoneInWords {
    display: none;
  }
  .teamLogo {
    height: 25px;
    width: auto;
  }
  .teamLogoContainer {
    height: 100%;
    min-height: 50px;
    align-items: center;
  }
  #prospect-column, #team-dropdown-column {
    top: 12px;
    left: 175px;
  }
}
